<template>
  <div class="content_specific">
    <ul>
      <li v-for="value in datalist" :class="now(value)">
        <div class="left">
          <div>
            <p>{{ value.showAt | time }}</p>
            <p>{{ value.endAt | time }}散场</p>
          </div>
          <div>
            <p>{{ value.filmLanguage }}{{ value.imagery }}</p>
            <p>{{ value.hallName }}</p>
          </div>
        </div>
        <div class="right">
          <p>￥<span>{{ value.salePrice | price }}</span></p>
          <button type="button">{{ now(value) === "" ? "购票" : "停售" }}</button>
        </div>
      </li>
    </ul>
    <div class="ts">{{ !datalist.length === 0 ? '暂无场次' : '到底啦~~~' }}</div>
  </div>
</template>

<script>
  import Vue from "vue"
  import {Toast} from "vant";

  Vue.use(Toast);
  Vue.filter("time", value => {
    let d = new Date(value * 1000)
    if (d.getMinutes() < 10) {
      return d.getHours() + ":0" + d.getMinutes()
    } else {
      return d.getHours() + ":" + d.getMinutes()
    }
  })
  Vue.filter("price", value => {
    return value / 100
  })
  export default {
    data() {
      return {
        isShow: false,
      }
    },
    props: {
      "datalist": {
        type: Array,
        default() {
          return []
        }
      }
    },
    methods: {
      now(value) {
        if (value.showAt * 1000 <= new Date().getTime() + 300) {
          return "no"
        } else {
          return ""
        }
      }
    },
    mounted() {
    },

  }
</script>

<style scoped lang="scss">
  .content_specific {
    width: 100%;

    .ts {
      display: flex;
      align-items: center;
      justify-content: center;
      color: #5d5d5d;
      font-size: 12px;
      height: 60px;
    }

    ul {
      transition: all 200ms ease-in-out;

      .no {
        color: #d2d6dc !important;

        .left {
          color: #d2d6dc !important;


          div {
            color: #d2d6dc !important;


            p:nth-of-type(1) {
              color: #d2d6dc !important;

            }

            p:nth-of-type(2) {
              color: #d2d6dc !important;

            }
          }

          div:nth-of-type(2) {
            color: #d2d6dc !important;

          }
        }

        .right {
          color: #d2d6dc !important;

          p {
            color: #d2d6dc !important;

            span {
              color: #d2d6dc !important;
            }
          }

          button {
            color: #d2d6dc !important;
            border: 1px solid #d2d6dc;
          }
        }
      }

      li {
        width: 100%;
        height: 74px;
        padding: 15px 15px;
        background: #fff;
        display: flex;
        border-bottom: 1px solid #ededed;
        box-sizing: border-box;
        justify-content: space-between;

        .left {
          flex: 1;
          display: flex;


          div {
            width: 84px;

            p:nth-of-type(1) {
              font-size: 15px;
              color: #191a1b;
              white-space: nowrap;
              text-overflow: ellipsis;
              overflow: hidden;
            }

            p:nth-of-type(2) {
              font-size: 13px;
              color: #797d82;
              margin-top: 2px;
              white-space: nowrap;
              text-overflow: ellipsis;
              overflow: hidden;
            }
          }

          div:nth-of-type(2) {
            width: 0;
            flex: 1;
            margin-right: 25px;
          }
        }

        .right {
          display: flex;
          align-items: center;

          p {
            margin-right: 20px;
            font-size: 10px;
            color: #ff5f16;

            span {
              font-size: 15px;
            }
          }

          button {
            height: 25px;
            width: 50px;
            color: #ff5f16;
            border: 1px solid #ff5f16;
            border-radius: 2px;
            background: inherit;
            font-size: 12px;
          }
        }
      }

    }
  }
</style>
